$matboardDepth: 1px;
$matSize: 50px;
$scale: 1;
$width: 500px;
$frameThickness: 15px;

.background {
  //height: 1200px;
  //width: 1200px;
  display: flex;
  align-content: center;
  justify-content: center;
  background: rgb(230, 230, 230);
  background: linear-gradient(11deg, rgba(230, 230, 230, 1) 0%, rgb(246, 246, 246) 100%);
  z-index: -1;
}

.mat {
  position: relative;
  padding: $matSize * $scale;
  padding-bottom: $matSize * $scale;
  width: auto;
  background: linear-gradient(63deg, rgba(255, 255, 255, 1) 0%, rgba(254, 254, 254, 1) 100%);
  background: #f7f6f6;
  z-index: -1;
}

.mat-black {
  background: linear-gradient(63deg, rgba(255, 255, 255, 1) 0%, rgba(254, 254, 254, 1) 100%);
  background: #2a2a2a;
}

/*Mat cut*/
.image {
  position: relative;
  z-index: -1;
  width: $width;
}

/* Image settings only when mat is present */
.mat .image {
  border-right: solid $matboardDepth #ddd;
  border-top: solid $matboardDepth #ccc;
  border-bottom: solid $matboardDepth #fff;
  border-left: solid $matboardDepth #fff;
}

.mat.circle .image {
  border-radius: 9999px;
  border: none !important;
  box-shadow: 0 -3px 0 0 rgba(0, 0, 0, .1), 0 3px 0 0 rgba(255, 255, 255, 1);
}

.frame {
  align-self: center;
  align-content: center;
  box-shadow: -15px 15px 50px 0 rgba(0, 0, 0, 0.3),
  -3px 3px 10px 1px rgba(0, 0, 0, 0.3),
  -8px 5px 10px 0px rgba(0, 0, 0, 0.2) inset,
  2px -3px 10px 0px rgba(0, 0, 0, 0.1) inset;
}

.frame-prematted {
  box-shadow: -15px 15px 50px 0 rgba(0, 0, 0, 0.3),
  -3px 3px 10px 1px rgba(0, 0, 0, 0.1);
}

.frame-black {
  border: $frameThickness solid #111;
  border-bottom: $frameThickness solid #222;
  border-left: $frameThickness solid #222;
}

.frame-white {
  border: $frameThickness solid #efefef;
  border-bottom: $frameThickness solid #f5f5f5;
  border-left: $frameThickness solid #f5f5f5;
}

.frame-wood {
  border: $frameThickness solid transparent;
  border-image: url(data:image/jpeg;base64,) 49 49 49 49 round round;
  box-shadow: -15px 15px 50px 0 rgba(0, 0, 0, 0.3),
  -3px 3px 10px 1px rgba(0, 0, 0, 0.3),
  -2px 2px 3px 0px rgba(151, 129, 107, 0.5) inset,
  -8px 5px 10px 0px rgba(0, 0, 0, 0.2) inset,
  2px -3px 10px 0px rgba(0, 0, 0, 0.1) inset;
}

.label {
  font-family: Avenir;
  padding: 15px 25px;
  border-radius: 9999px;
  color: #888;
  bottom: 50px;
  font-size: 2em;
  position: absolute;
  margin: 0 auto;
}